import React, { useContext } from "react"
import { makeStyles, useTheme } from "@material-ui/core/styles"
import CentreContainer from "@/components/Container"
import { FaFacebook, FaInstagram, FaTwitter, FaYoutube } from "react-icons/fa"
import ContextStore from "@/contextStore"
import { FOOTER_IS_SHOWING } from "@/reducers/footer"
import { useTranslation } from "react-i18next"
import { ExternalLink } from "@/components/Link"
import { InView } from "react-intersection-observer"

const useStyles = makeStyles(theme => ({
  root: {
    background: theme.palette.primary.main,
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(8),
  },
  footerRow: {
    display: "grid",
    gridTemplateColumns: `1fr`,
    textAlign: "center",
    [theme.breakpoints.up("viewport7")]: {
      maxWidth: "100%",
      textAlign: "left",
    },
  },
  iconRow: {
    display: "flex",
    width: "200px",
    marginLeft: "auto",
    marginRight: "auto",
    justifyContent: "space-between",
    [theme.breakpoints.up("viewport7")]: {
      marginLeft: 0,
      justifyContent: "flex-start",
    },
  },
  iconWrapper: {
    cursor: "pointer",
    [theme.breakpoints.up("viewport7")]: {
      marginRight: theme.spacing(3),
    },
  },
  importantLinksRow: {
    display: "flex",
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(3),
    marginLeft: "auto",
    marginRight: "auto",
    gridGap: theme.spacing(4),
    width: "100%",
    justifyContent: "space-evenly",
    "& a": {
      cursor: "pointer",
    },
    [theme.breakpoints.up("viewport7")]: {
      marginLeft: 0,
      justifyContent: "flex-start",
      "& a": {
        marginRight: theme.spacing(3),
      },
    },
  },
}))

const socialMedias = ({ size, color }) => {
  return [
    {
      icon: <FaFacebook size={size} color={color} />,
      url: "https://www.facebook.com/hkcnews",
    },
    {
      icon: <FaInstagram size={size} color={color} />,
      url: "https://www.instagram.com/hkcnews",
    },
    {
      icon: <FaTwitter size={size} color={color} />,
      url: "https://twitter.com/hkcnews_com",
    },
    {
      icon: <FaYoutube size={size} color={color} />,
      url: "https://www.youtube.com/channel/UC7K4DBOzdITZFOjGkea_CCA",
    },
  ]
}

const importantLinks = ["privacy", "terms", "contact_us"]

export default function Footer() {
  const classes = useStyles()

  const {
    footer: { dispatch },
  } = useContext(ContextStore)

  const { t } = useTranslation()
  const theme = useTheme()

  const onScreen = inView => {
    dispatch({
      type: FOOTER_IS_SHOWING,
      isShowing: inView,
    })
  }

  return (
    <InView as="footer" className={classes.root} onChange={onScreen}>
      <CentreContainer>
        <div className={classes.footerRow}>
          <div className={classes.iconRow}>
            {socialMedias({
              color: theme.palette.secondary.main,
              size: 24,
            }).map(sm => {
              return (
                <div
                  key={sm.url}
                  className={classes.iconWrapper}
                  onClick={() => {
                    window.open(sm.url, "_blank")
                  }}
                >
                  {sm.icon}
                </div>
              )
            })}
          </div>
        </div>
        <div className={classes.footerRow}>
          <div className={classes.importantLinksRow}>
            {importantLinks.map(link => {
              return (
                <ExternalLink
                  key={link}
                  to={t(`site.${link}_url`)}
                  style="plain"
                >
                  {t(`site.${link}`)}
                </ExternalLink>
              )
            })}
          </div>
        </div>
        <div className={classes.footerRow}>{t("site.copyright")}</div>
      </CentreContainer>
    </InView>
  )
}
